.device-card {
    background-color: #fff;
    color: rgba(52, 49, 76, 1);
    -webkit-transition: box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    transition: box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    border-radius: 4px;
    box-shadow: 0px 2px 1px -1px rgb(0 0 0 / 20%), 0px 1px 1px 0px rgb(0 0 0 / 14%), 0px 1px 3px 0px rgb(0 0 0 / 12%);
    width: 100%;
    padding: 20px;
}

.device__box {
    height: 78px;
    display: flex;
    justify-content: space-between;
}

.device__box-item {
    flex-grow: 1;
}

.device-container{
    padding: 10px;
    align-items: baseline;
}

.device-breadcrumb{
    padding: 12px;
}

.add-button{
    margin-left: auto;
    margin-right: 1%;
    height: min-content;
}

.device-box{
    width: 100%;
    padding: 10px;
    display: flex;
    flex-direction: row;
}

.device-search{
    width: 40%;
}

.select-role{
    margin-left: auto;
    margin-right: 0;
}

.device-wrap {
    width: 450px;
    overflow: auto;
}